.footer{
  background-image: url(/../../img/head-footer.jpg);
  background-position: top;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  &__img{
    background-color: rgba(255, 255, 255, 0.7);
    background-image: url(../../img/footer-person.png);
    background-position: 50px bottom;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &__text{
    padding: 0 0 60px 0;
    background-color: rgba(1,40,72,.95);
  }
  &__form{
    padding-top: 100px;
    padding-bottom: 30px;
    @media screen and (max-width: 768px){
      padding-top: 50px;
    }
    .button{
      width: 100%;
    }
  }
}

.soc {
  width: 100px;
  display: flex;
  justify-content: space-between;
  font-family: "icons";
  font-size: 16px;
  color: #fff;
  @media screen and (max-width: 768px){
    margin: 20px 0;
  }
  a{
    color: #153853;
    text-decoration: none;
    width: 27px;
    height: 27px;
    text-align: center;
    line-height: 27px;
    border-radius: 3px;
    background-color: #fff;
    &:hover{
      transition: .2s;
      background-color: rgba(255,255,255,0.5)
    }
  }
  i{
    font-style: normal;
  }
}
.contact{
  list-style: none;
  padding: 0;
  margin: 0;
  &__item{
    padding: 10px 0;
    font-style: normal;
    display: inline-block;
    width: 49%;
    font-family: "icons";
    @media screen and (max-width: 600px){
      display: block;
      width: 100%;
      padding: 0;
    }
    i{
      color: #37a1f2;
      font-size: 10px;
      margin-right: 5px;
      display: inline-block;
      vertical-align: middle;
      font-style: normal;
    }
    a{
      font-size: 16px;
      color: #ffffff;
      text-decoration: none;
      font-weight: 400;
      font-style: normal;
      text-decoration: none;
      display: block;
        @media screen and (max-width: 1400px){
          font-size: 15px;
        }
      &:hover{
        transition: .2s;
        color: #37a1f2;
      }
    }
    span{
      font-size: 16px;
      color: #ffffff;
      text-decoration: none;
      font-weight: 400;
      font-style: normal;
      text-decoration: none;
      display: block;
      @media screen and (max-width: 1400px){
        font-size: 15px;
      }
    }
  }
}

.copy{
  color: #fff;
  margin-top: 20px;
  @media screen and (max-width: 768px){
    margin-top: 0;
  }
}