.correct{
  padding: 100px 0;
  @media screen and (max-width: 768px){
    padding: 50px 0;
    .block-title{
      margin-top: 0;
    }
  }
  &__icon{
    font-family: "icons";
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    border-radius: 5px;
    background-color: $dark-blue;
    margin-right: 10px;
    @media screen and (max-width: 768px){
      width: 30px;
      height: 30px;
    }
  }
  &__text{
    padding-left: 50px;
  }
}
.icon-correct{
  color: #fff;
  font-size: 20px;
  line-height: 40px;
  position: relative;
  right: 3px;
  @media screen and (max-width: 768px){
    font-size: 16px;
    line-height: 30px;
  }
}