.services{
  padding: 100px 0;
  @media screen and (max-width: 768px){
    padding: 50px 0;
  }
  &__img-wrap{
    position: relative;
    overflow: hidden;
    transition: .3s;
    border: 16px solid;
    border-color: #ffffff;
    box-shadow: 0 3px 10px rgba(38,72,101,0.23);
  }
  &__img{
    width: 100%;
  }
}
.list{
  list-style: none;
  padding: 0;
  margin: 0;
  padding-left: 10px;
  padding-top: 30px;
  li{
    position: relative;
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
    &:before{
      position: absolute;
      content: '';
      transition: .3s;
      left: -15px;
      -webkit-transform: translate(0,-50%);
      transform: translate(0,-50%);
      -ms-transform: translate(0,-50%);
      width: 6px;
      height: 6px;
      background: #37a1f2;
      top: 11px;
    }
  }
}