.input{
  font-size: 16px;
  padding: 10px 20px;
  line-height: normal;
  outline: none;
  -webkit-appeareance: none;
  width: 100%;
  height: 46px;
  color: #002746;
  background-color: #ffffff;
  font-style: normal;
  font-weight: 400;
  border-color: transparent;
  @media screen and (max-width: 1366px){
    padding: 10px;
  }
  &--border{
    border-width: 1px;
    border-style: solid;
    border-color: $dark-blue;
    &::placeholder{
      color: #002746;
    }
  }
}

.form{
  label{
    display: block;
    margin-bottom: 15px;
  }
  textarea{
    height: 120px;
  }
}