@import "./partials/normalize";
@import "./partials/variables";
@import "./partials/iconfont";
@import "./partials/sprite";
@import "./partials/grid";
@import "./partials/header";
@import "./partials/head";
@import "./partials/about";
@import "./partials/advantage";
@import "./partials/services";
@import "./partials/correct";
@import "./partials/reviews";
@import "./partials/popup";
@import "./partials/countdown";
@import "./partials/form";
@import "./partials/footer";

body{
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1;
    color: #434f53;
    background-color: #fff;
    position: relative;
}

.wrap{
    overflow: hidden;
}

.section{
    padding: 100px 0;
    position: relative;
    background-color: rgba(1, 40, 72, 0.95);
    @media screen and (max-width: 768px){
        padding: 50px 0;
    }
}

p, li{
    font-size: 14px;
    line-height: 26px;
}

img{
    max-width: 100%;
}

a{
    text-decoration: none;
}

.title{
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: 37px;
    line-height: 1.2;
    display: inline-block;
    letter-spacing: 0;
    margin: 0;
    margin-bottom: 40px;
    @media screen and (max-width: 768px){
        margin-bottom: 30px;
        font-size: 27px;
    }
    @media screen and (max-width: 600px){
        font-size: 22px;
    }
    &--blue{
        color: #153853;
        position: relative;
        &:before{
            content: '';
            width: 50px;
            height: 1px;
            background: #153853;
            display: inline-block;
            position: relative;
            bottom: 10px;
            margin-right: 10px;
            @media screen and (max-width: 768px){
                width: 30px;
            }
        }
    }
    &--white{
        color: #fff;
        position: relative;
        &:before{
            content: '';
            width: 50px;
            height: 1px;
            background: #fff;
            display: inline-block;
            position: relative;
            bottom: 10px;
            margin-right: 10px;
            @media screen and (max-width: 768px){
                width: 30px;
            }
        }
    }
    &--center{
        text-align: center;
        display: block;
    }
}

.block-title{
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: 25px;
    line-height: 1.2;
    letter-spacing: 0;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    @media screen and (max-width: 768px){
        margin-top: 30px;
        font-size: 20px;
    }
    @media screen and (max-width: 600px){
        font-size: 19px;
    }
    &--blue{
        color: #153853;
        position: relative;
        &:after{
            content: '';
            width: 25px;
            height: 1px;
            background: #37a1f2;
            position: absolute;
            bottom: -15px;
            left: 0;
        }
    }
    &--white{
        color: #fff;
        position: relative;
        &:after{
            content: '';
            width: 25px;
            height: 1px;
            background: #fff;
            position: absolute;
            bottom: -15px;
            left: 0;
        }
    }
}

.button{
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #ffffff;
    outline: none;
    position: relative;
    text-align: center;
    display: inline-block;
    z-index: 1;
    line-height: 1;
    border-width: 0;
    transition: .3s;
    border-radius: 10px;
    font-size: 16px;
    padding: 20.5px 58.5px;
    cursor: pointer;
    @media screen and (max-width: 996px){
        padding: 15px 30px;
    }
    &--blue{
        background-color: #248add;
        &:hover{
            background-color: #1b2f3d;
        }
    }
    &--red{
        background-color: #ec5252;
        padding: 15px 30px;
        &:hover{
            background-color: #0d3250;
        }
    }
    &--full-width{
        width: 100%;
        display: block;
        text-align: center;
    }
}


