.header{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  z-index: 1;
  &__phone{
    font-family: Raleway,sans-serif;
    font-weight: 400;
    font-style: normal;
    display: block;
    color: #fff;
    font-size: 35px;
    line-height: 1;
    letter-spacing: 0;
    margin: 0 0 20px 0;
    text-decoration: none;
    @media screen and (max-width: 1366px){
      font-size: 27px;
    }
    @media screen and (max-width: 768px){
      color: #153853;
      font-size: 17px;
      margin-bottom: 5px;
    }
  }
  &__info{
    text-align: right;
    padding-top: 30px;
    @media screen and (max-width: 768px){
      padding-top: 10px;
      margin: 0 -15px;
    }
  }
  &__title{
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-style: normal;
    color: #248add;
    font-size: 100px;
    line-height: 1;
    letter-spacing: 0;
    margin: 0;
  }
}