.popup{
  width: 400px;
  height: 340px;
  text-align: center;
  padding: 50px 15px 30px 15px;
  background: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  display: none;
  box-shadow: 0 3px 9px rgba(0,0,0,.5);
  @media screen and (max-width: 768px){
    width: 280px;
    height: 290px;
    padding: 40px 15px 30px 15px;
  }
  &-bg{
    background: rgba(1, 40, 72, 0.8);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    display: none;
  }
  &__close{
    width: 22px;
    height: 22px;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 1;
    color: #333333;
    cursor: pointer;
    font-size: 28px;
    opacity: 0.7;
    &:hover{
      opacity: 1;
    }
  }
  &__title{
    font-family: Raleway,sans-serif;
    font-weight: 500;
    font-style: normal;
    color: #012848;
    font-size: 26px;
    line-height: 1.2;
    letter-spacing: 0;
    position: relative;
    margin-bottom: 30px;
    @media screen and (max-width: 768px){
      font-size: 20px;
      margin-bottom: 20px;
    }
  }
}