.head {
  background-image: url(/../../img/head-footer.jpg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  &__text {
    padding: 100px 0;
    background-color: rgba(255, 255, 255, 0.7);
    @media screen and (max-width: 768px) {
      padding-bottom: 50px;
    }
  }
  &__img {
    display: none;
    @media screen and (min-width: 768px) {
      display: block;
      background-color: rgba(1, 40, 72, 0.95);
      background-image: url(../../img/head-person.png);
      background-position: 50px bottom;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  &__title {
    font-family: 'Raleway', sans-serif;
    font-weight: 800;
    font-style: normal;
    color: #248add;
    font-size: 100px;
    line-height: 1;
    letter-spacing: 0;
    margin: 0;
    @media screen and (max-width: 1700px) {
      font-size: 80px;
    }
    @media screen and (max-width: 1100px) {
      font-size: 66px;
    }
    @media screen and (max-width: 996px) {
      font-size: 44px;
    }
    @media screen and (max-width: 768px) {
      font-size: 30px;
    }
  }
  &__subtitle {
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #012848;
    font-size: 26px;
    line-height: 1.2;
    letter-spacing: 0;
    position: relative;
    @media screen and (max-width: 1366px) {
      font-size: 22px;
    }
    @media screen and (max-width: 996px) {
      font-size: 20px;
      margin: 0;
    }
    @media screen and (max-width: 768px) {
      font-size: 18px;
    }
    &:after {
      content: '';
      width: 50px;
      height: 2px;
      background: #248add;
      position: absolute;
      bottom: -15px;
      left: 0;
    }
  }
  &__left {
    padding-top: 100px;
  }
  &__about {
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    font-style: italic;
    color: #153853;
    font-size: 18px;
    line-height: 1.4;
    letter-spacing: 0;
    padding: 30px 0 15px 0;
    @media screen and (max-width: 996px) {
      font-size: 16px;
    }
    span {
      font-weight: 400;
    }
  }
  &__timer {
    background: rgba(15, 137, 230, 0.75);
    padding: 10px 20px 24px 20px;
    &-text {
      color: #fff;
    }
  }
}
.timer {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (max-width: 1700px) {
    display: block;
    width: 100%;
  }
  &__time {
    width: 200px;
    @media screen and (max-width: 1700px) {
      margin: auto;
      overflow: hidden;
    }
  }
  &__button {
    @media screen and (max-width: 1700px) {
      margin-top: 20px;
      text-align: center;
    }
  }
}
