.reviews{
  padding: 100px 0;
  @media screen and (max-width: 768px){
    padding: 50px 0;
  }
}
.review{
  &__img{
    display: inline-block;
    vertical-align: middle;
    width: 150px;
  }
  &__info{
    display: inline-block;
    vertical-align: middle;
    padding-left: 10px
  }
  &__name{
    font-family: Raleway,sans-serif;
    font-weight: 600;
    color: #153853;
    font-style: normal;
    display: block;
    font-size: 22px;
    line-height: 1.2;
    letter-spacing: 0;
    margin: 0;
    @media screen and (max-width: 768px){
      font-size: 18px;
      line-height: 1;
    }
  }
  &__date{
    font-family: Raleway,sans-serif;
    font-weight: 400;
    color: #153853;
    font-style: normal;
    font-size: 22px;
    line-height: 1.2;
    letter-spacing: 0;
    margin: 0;
    @media screen and (max-width: 768px){
      font-size: 18px;
      line-height: 1;
    }
  }
}